import Axios from 'axios'
// import Qs from 'qs'
import { Toast } from '@/components/common/Toast'
import LightSDK from 'light-sdk/dist/index.umd.js'

const getToken = () => {
  return new Promise((resolve, reject) => {
    const params = {
      key: 'token',
      scope: 'global'
    }
    LightSDK.native.readData(params, ({ info, data }) => {
      if (info.error_code === '0') {
        try {
          resolve(JSON.parse(data.result))
        } catch (error) {
          resolve(data.result)
        }
      } else {
        reject(info)
      }
    })
  })
}
const instance = Axios.create({
  baseURL: 'http://uplus.sit.hs.net'
  // headers: {
  //   'X-GTN-Access-Token': '2952cee6-8285-420a-94ff-521bb4fcfc021'
  //   // 'Content-Type': 'application/x-www-form-urlencoded'
  // }
  // transformRequest: [params => {
  //   // 对 data 进行任意转换处理
  //   return Qs.stringify(params)
  // }]
})

instance.interceptors.request.use(config => {
  return getToken().then(token => {
    config.headers['X-GTN-Access-Token'] = token
    return config
  }).catch(_error => {
    console.log(_error)
  })
  // return config
})

instance.interceptors.response.use(response => {
  const { data } = response
  if (data.code === 0) {
    return Promise.resolve(data.data ? data.data : data.rows)
  } else {
    console.log(data)
    Toast(data.error_info)
    return Promise.reject(data)
  }
}, _error => {
  return Promise.reject(_error)
})
export default instance
