<template>
    <div class="container">
        <div class="left">
            <p class="score">{{ score }}</p>
            <div>
                <rate :score="score" />
                <p>{{ statistics.total }}条评论</p>
            </div>
        </div>
        <div class="right">
            <x-progress
                class="item"
                v-for="(item, index) in progressData"
                :info="item"
                :key="index" />
        </div>
    </div>
</template>

<script>
import Rate from '@/components/Rate'
import XProgress from '@/components/common/Progress'
export default {
  props: {
    score: {
      type: Number,
      default: 10
    },
    statistics: {
      type: Object,
      default: () => {}
    }
  },
  computed: {
    progressData () {
      const { good, centre, bad } = this.statistics
      return [{ text: '好评', data: good }, { text: '中评', data: centre }, { text: '差评', data: bad }]
    }
  },
  name: 'statistics',
  data () {
    return {}
  },
  components: { Rate, XProgress }
}
</script>

