<template>
    <div class="rate">
        <div @click="handleSelect(i)"  v-for="i in 5" :key="i">
            <img v-if="i * 2 <= score" :style="iconStyle" src="../assets/images/icon_star.png" />
            <img v-else :style="iconStyle" src="../assets/images/icon_star_gray.png" />
        </div>
    </div>
</template>
<script>
export default {
  model: {
    prop: 'score',
    event: 'change'
  },
  props: {
    score: {
      type: Number
    },
    size: {
      type: Number
    },
    disabled: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
    }
  },
  computed: {
    iconStyle () {
      if (!this.size) return {}
      return {
        width: this.size + 'rem',
        height: this.size + 'rem'
      }
    }
  },
  methods: {
    handleSelect (i) {
      if (this.disabled) return
      this.$emit('change', i * 2)
    }
  }
}
</script>

<style lang="scss" scoped>
.rate{
    display: flex;
    flex-direction: row;
    img {
        width: 24px;
        height: 24px;
        display: block;
    }
}
</style>
