<template>
    <div @click.self="$emit('update:visible', false)" v-if="visible" class="mask">
        <div class="main">
            <div @click="$emit('update:visible', false)" class="close">
                <img src="../assets/images/icon_close.png" alt="">
            </div>
            <h2>{{ title }}</h2>
            <div class="mark">
                <rate :disabled="false" v-model="score" class="center" :size="0.5" />
                <p>{{ commentMap[score] }}</p>
            </div>
            <textarea v-model="value" placeholder="有什么话想对老师说的吗？来说说吧（至少5个字）" name="" id=""></textarea>
            <button @click="handleSubmit">提交</button>
        </div>
    </div>
</template>

<script>
import Rate from './Rate'
const commentMap = {
  0: '轻触评分',
  2: '极差，课程很槽糕，我要吐槽',
  4: '差，我对课程不满意',
  6: '中评，课程很一般',
  8: '良好，课程还可以',
  10: '推荐，课程非常棒'
}
export default {
  props: {
    title: {
      type: String
    },
    courseId: {
      type: Number
    },
    visible: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      value: '',
      score: 0,
      commentMap
    }
  },
  components: {
    Rate
  },
  methods: {
    handleSubmit () {
      if (this.score === 0) {
        return
      }
      const params = {
        scope: this.score,
        content: this.value || commentMap[this.score]
      }
      this.$emit('on-submit', params)
    }
  }
}
</script>

