<template>
  <div id="app" :class="{ app: !hasJoin }">
   <video-player
       v-show="hasJoin"
       ref="videoPlayer"
       class="video-player vjs-custom-skin"
       :playsinline="true"
       :options="playerOptions"
       @play="onPlayerPlay($event)"
       @pause="onPlayerPause($event)"
       @ended="onPlayerEnded($event)"
      />
      <x-header v-if="!hasJoin" class="header-fix bg-white" :title="courseInfo.title"></x-header>
      <tool 
        v-else
       class="header-fix"
       @on-right="handleCollection"></tool>
      <div v-if="!hasJoin" class="info">
        <img class="banner" :src="courseInfo.cover" alt="">
        <div class="info-content">
          <h3>{{ courseInfo.name }}</h3>
          <div>
            <rate :score="courseInfo.scope" />
            <span>{{ courseInfo.joined }}人选修</span>
          </div>
          <p>讲师:{{ courseInfo.teacherName || '未知' }}</p>
          <p>有效期:{{ courseInfo.begin | timeStampFormatDate }}至{{ courseInfo.end | timeStampFormatDate }}</p>
        </div>
      </div>
      <tab :tabs="tabs" :hasJoin="hasJoin" :tab-key.sync="tabKey" />
      <div class="split"></div>
      <introduction 
        v-show="tabKey === 0" 
        :desc="courseInfo.description" 
        :teacher="teacherInfo" />
      <catalog
        :currentId="lessonId"
        v-show="tabKey === 1"
        :catalogs="catalogs"
        :courseId="courseId"
        :hasJoin="hasJoin"
        @change-video="handleChangeVideo" />
      <evaluate
        v-show="tabKey === 2"
        :score="courseInfo.scope"
        :statistics="statistics"
        :hasJoin="hasJoin"
        :evaluations="evaluations"
        @on-evaluate="visibleCreateEvaluate = true" />
      <bottom-action
        v-if="!hasJoin"
        :isCollected="courseInfo.isCollection"
        @on-joined="handleJoin"
        @on-collection="handleCollection"
        class="bottom-fix" />
      <create-evaluate
        :title="courseInfo.title"
        :visible.sync="visibleCreateEvaluate"
        @on-submit="handleCreateEvaluate" />
  </div>
</template>

<script>
import 'videojs-contrib-hls'
import XHeader from '@/components/Header'
import Tool from '@/components/Tool'
import Rate from '@/components/Rate'
import Tab from '@/components/Tab'
import Introduction from '@/components/Introduction'
import Catalog from '@/components/Catalog/Index'
import Evaluate from '@/components/Evaluate/Index'
import BottomAction from '@/components/BottomAction'
import CreateEvaluate from '@/components/CreateEvaluate'
import API from '@/api/index'
const tabs = [
  {
    key: 0,
    text: '介绍'
  },
  {
    key: 1,
    text: '目录',
    icon: require('./assets/images/icon_play_title.png')
  },
  {
    key: 2,
    text: '评价'
  }
]
export default {
  name: 'app',
  data () {
    return {
      courseId: '',
      teacherId: '',
      courseInfo: {},
      teacherInfo: {},
      visibleCreateEvaluate: false,
      tabKey: 0,
      tabs,
      title: '',
      catalogs: [],
      statistics: {},
      evaluateQuery: {
        page: 1,
        rows: 10
      },
      lessonId: '', // 当前播放的课时Id
      evaluations: [],
      playerOptions: {
        // playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: false, // 如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: false, // 导致视频一结束就重新开始。
        preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: 'zh-CN',
        aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        // sources: [
        //   {
        //     type: 'video/mp4', // 这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
        //     src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm' // url地址
        //   }
        // ],
        sources: [
          {
            withCredentials: false,
            type: 'application/x-mpegURL',
            src: 'http://122.224.126.29:8080/20161110/34/34_20161110102801.m3u8'
          }
        ],
        hls: true,
        poster: '', // 你的封面地址
        width: document.documentElement.clientWidth, // 播放器宽度
        notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true // 全屏按钮
        }
      }
    }
  },
  components: {
    XHeader,
    Rate,
    Tab,
    Introduction,
    Catalog,
    Evaluate,
    BottomAction,
    CreateEvaluate,
    Tool
  },
  computed: {
    hasJoin () {
      return this.courseInfo.subscribed
    },
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  watch: {
    hasJoin(val) {
      val && this.catalogs.length !== 0  && this.getVideo(this.lessonId)
    },
    catalogs() {
      this.hasJoin && this.getVideo(this.lessonId)
    }
  },
  mounted () {
    const searchParams = new URLSearchParams(location.search.slice(1))
    this.courseId = searchParams.get('courseId')
    this.teacherId = searchParams.get('teacherId')
    this.getCourseDetail()
    this.teacherId && this.getTeacher()
    this.getCatalogs()
    this.getEvaluations()
    this.getStatistics()
  },
  methods: {
    getCourseDetail () {
      API.DETAIL({ courseId: this.courseId })
        .then(data => {
          data.subscribed && (this.tabKey = 1)
          this.courseInfo = data
          this.playerOptions.poster = data.cover
        })
        .catch(_error => {
        })
    },
    getTeacher () {
      API.TEACHER({ teacherId: this.teacherId })
        .then(data => {
          this.teacherInfo = data
        })
        .catch(_error => {
        })
    },
    getCatalogs () {
      API.CATALOG({ courseId: this.courseId })
        .then(data => {
          this.catalogs = data
          this.lessonId = data[0].id
          // this.getVideo(data[0].id)
        })
        .catch(_error => {
          this.catalogs = []
        })
    },
    getVideo (id) {
      API.VIDEO({ lesson: id, index: 1 })
        .then(data => {
          console.log(data)
          this.player.src({
            type: 'application/x-mpegURL',
            src: data.url
          })
        })
        .catch(_error => {

        })
    },
    getEvaluations () {
      API.EVALUATION({ courseId: this.courseId, ...this.evaluateQuery })
        .then(data => {
          this.evaluations = data
        })
        .catch(_error => {

        })
    },
    getStatistics () {
      API.STATISTICS({ courseId: this.courseId }).then(data => {
        this.statistics = data
      }).catch(_error => {

      })
    },
    handleChangeVideo (id) {
      this.getVideo(id)
    },
    handleJoin () {
      API.JOIN_COURSE({ courseId: this.courseId })
        .then(() => {
          this.getCourseDetail()
        })
        .catch()
    },
    handleCollection () {
      const { isCollection } = this.courseInfo
      const params = {
        courseId: this.courseId,
        status: isCollection ? 0 : 1
      }
      API.COLLECT(params)
        .then(data => {
          this.courseInfo.isCollection = !isCollection
          this.$toast(`${ isCollection ? '取消' : '收藏'}成功`)
        })
        .catch(_error => {
          console.log(_error)
          this.$toast(`${isCollection ? '取消' : '收藏'}失败`)
        })
    },
    handleCreateEvaluate (data) {
      API.CREATE_EVALUATE({ ...data, courseId: this.courseId })
        .then(data => {
          this.$toast('评价成功')
          this.getStatistics()
          this.getEvaluations()
          this.visibleCreateEvaluate = false
        })
        .catch(_error => {
          this.$toast('评价失败')
        })
    },
    onPlayerPlay () {
      console.log('play')
      API.LESSON_START({ lesson: this.lessonId })
    },
    onPlayerPause () {},
    onPlayerEnded() {
      console.log('ended')
      API.LESSON_END({ lesson: this.lessonId })
    }
  }
}
</script>

<style lang="scss">
.app {
  padding-bottom: 72px;
  padding-top: 88px;
}
.info {
  padding: 32px 40px;
}
.banner {
  display: block;
  width: 670px;
  height: 308px;
  border-radius: 16px;
}

.info-content {
  color: #999999;
  font-size: 28px;
  padding-top: 32px;
  div {
    height: 64px;
    font-size: 26px;
    display: flex;
    align-items: center;
    span {
      margin-left: 32px;
    }
  }
  h3 {
    color: #333333;
    font-size: 40px;
  }
  p:last-child {
    margin-top: 16px;
  }
}

.split {
  width: 100%;
  height: 12px;
  background: #F5F5FC;
}

.bottom-fix {
  position: fixed;
  width: 750px;
  bottom: 0;
  left: 0;
}

.header-fix {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
}

.bg-white {
  background-color: #FFFFFF;
}
</style>
