<template>
    <div class="item">
        <div class="flex-center">
            <div class="name flex-center">
                <p>{{index + 1}}.{{ item.title }}</p>
                <img v-if="hasJoin" :src="playIcon" alt="">
            </div>
            <div @click.stop="handlePraise" class="praise flex-center">
                <img :src="praiseIcon" alt="">
                <span>{{ item.fabulous }}</span>
            </div>
        </div>
        <p class="time">{{ item.time | formatTime }}</p>
    </div>
</template>

<script>
import API from '@/api'
export default {
  name: 'item',
  props: {
    item: {
      type: Object
    },
    index: {
      type: Number
    },
    currentId: {
      type: [Number, String]
    },
    courseId: {
      type: [Number, String]
    },
    hasJoin: {
      type: Boolean
    }
  },
  computed: {
    praiseIcon () {
      return this.item.isFabulous ? require('@/assets/images/icon_praise_already.png') : require('@/assets/images/icon_praise.png')
    },
    playIcon () {
      return this.item.id === this.currentId ? require('@/assets/images/icon_playing.png') : require('@/assets/images/icon_play.png')
    }
  },
  filters: {
    formatTime (time) {
      let m = Math.floor(time / 1000 / 60)
      let s = time / 1000 % 60
      return `${m}分${s}秒`
    }
  },
  methods: {
    handlePraise () {
      const params = {
        courseId: this.courseId,
        lesson: this.item.id,
        status: this.item.isFabulous ? 0 : 1
      }
      API.PRAISE(params)
        .then(data => {
          this.item.isFabulous = this.item.isFabulous ? 0 : 1
          this.$toast(`${this.item.isFabulous ? '点赞' : '取消'}成功`)
        })
        .catch(_error => {
          this.$toast(`${this.item.isFabulous ? '取消' : '点赞'}成功`)
        })
    }
  }
}
</script>

