<template>
    <div class="tool">
        <div @click="handleLeft" class="left">
            <img src="../assets/images/icon_back_white@2x.png" alt="">
        </div>
        <div @click="handleRight" class="right">
            <img src="../assets/images/icon_collection_white.png" alt="">
        </div>
    </div>
</template>


<script>
import LightSDK from 'light-sdk/dist/index.umd.js'
export default {
    name: 'tool',
    methods: {
        handleLeft() {
            LightSDK.native.back({ number: 1 })
        },
        handleRight() {
            this.$emit('on-right')
        }
    }
}
</script>

