<template>
    <div class="bottom">
        <div @click="handleCollection" class="collection">
            <img :src="collectionIcon" alt="">
            <p :class="{ 'fc-blue': isCollected }">收藏</p>
        </div>
        <button @click="handleJoin">去学习</button>
    </div>
</template>

<script>
export default {
  name: 'bottomAction',
  props: {
    isCollected: {
      type: Boolean
    }
  },
  computed: {
    collectionIcon () {
      return this.isCollected ? require('../assets/images/icon_collectioned.png') : require('../assets/images/icon_collection.png')
    }
  },
  methods: {
    handleJoin () {
      this.$emit('on-joined')
    },
    handleCollection () {
      this.$emit('on-collection')
    }
  }
}
</script>

