/**
  * @name Tab
  * @desc tab换组件
  * @props { Array } tabs 渲染tab的数组, 数组子项必须包含text字段和key字段 必传
  * @props { String, Number } tabKey 选中的tab的key值 必传
  * @event tab-change 切换事件
  **/
<template>
    <div class="tab">
        <div
            :class="['tab-item', tabKey === item.key ? 'active' : '']"
            v-for="item in tabs"
            :key="item.key"
            @click="handleChange(item)">
            <p>{{ item.text }}</p>
            <img v-if="item.icon && hasJoin" :src="item.icon" alt="">
            <span 
            class="bar"
            :class="{ 'w96': hasJoin }"></span>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    tabs: {
      type: Array,
      required: true
    },
    tabKey: {
      type: [String, Number],
      required: true
    },
    hasJoin: {
      type: Boolean
    }
  },
  data () {
    return {

    }
  },
  methods: {
    handleChange (item) {
      this.$emit('tab-change', item)
      this.$emit('update:tabKey', item.key)
    }
  }
}
</script>

<style lang="scss" scoped>
.tab {
    width: 100%;
    height: 64px;
    line-height: 64px;
    display: flex;
    color: #999999;
}

.tab-item {
    position: relative;
    flex: 1;
    font-size: 32px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
}
.bar {
    position: absolute;
    bottom: 0;
    left: 50%;
    margin-left: -22px;
    width: 44px;
    height: 6px;
    border-radius: 2px;
}

.w96 {
  width: 96px;
  margin-left: -48px;
}

.active {
    color: #333333;
    .bar {
        background-image: linear-gradient(90deg, #6DA4FD 0%, #3B6BFA 100%);
    }
}
</style>
