/**
 * 时间戳转换为日期
 * @param {*} timeStamp
 */
const timeStampFormatDate = timeStamp => {
  if (!timeStamp) return ''
  let time = new Date(Number(timeStamp))
  let month = time.getMonth() + 1
  let day = time.getDate()
  return `${time.getFullYear()}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day}`
}

export default {
  timeStampFormatDate
}
